/* Variables */
:root {
  --black: #212529;
  --blue: #108de0;
  --green: #76c442;
  --red: #ce372b;
  --transparent: transparent;
  --white: white;
  --yellow: #f2c409;
  --grey: #D3D3D3;
  --background-color: var(--white);
  --text-color: var(--black);
  --error-color: var(--red);
  --primary-color: var(--blue);
  --success-color: var(--green);
  --warn-color: var(--yellow);
  --facebook: #4165b3;
  --github: #333333;
  --linkedin: #2477b9;
  --twitter: #009cf2; }

/* NES.css */
.nes-container.with-title {
  margin-top: var(--primary-gutter);
  position: relative; }
  .nes-container.with-title > .title {
    font-size: 1em;
    left: 0;
    margin: 0 !important;
    padding: 0 var(--primary-gutter);
    position: absolute;
    top: -1.8rem; }
  .nes-container.with-title.is-rounded > .title {
    left: 1rem; }

.nes-list.is-disc li:before {
  top: 5px; }

.nes-list ol, .nes-list ul {
  margin-left: 16px; }

/* NES.icons */
.nes-icon {
  height: auto;
  margin: 0;
  position: initial;
  width: auto; }
  .nes-icon::before {
    background: none;
    box-shadow: none !important;
    display: initial;
    left: 0;
    position: initial;
    top: 0; }
  .nes-icon:not([class*='rotate-']):not([class*='flip-']) {
    transform: none; }

/* NProgress */
#nprogress .bar {
  background-color: var(--black); }

#nprogress .peg {
  box-shadow: 0 0 1rem var(--black), 0 0 0.5rem var(--black); }

/* Prism */
.prism-code > .token-line:last-child {
  display: none; }
